import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import {  Paper  } from '@material-ui/core';


const styles = {
}

const TableCars = ({ classes, info }) => {  
  return (
    <div className={classes.root}>
     
              
            {info.map((item, index) => (
              <div className="car">
                <p><span>Marca:</span> {item.marca}</p>
                <p><span>Veculo:</span> {item.name}</p>
                <p><span>Ano:</span> {item.ano_modelo}</p>
                <p><span>Combustvel:</span> {item.combustivel}</p>
                <p><span>Ano referncia:</span> {item.referencia}</p>
                <p className="preco" ><span>Preo:</span> {item.preco}</p>
              </div>
            ))} 
           
       
      
    </div>
  );
}


export default withStyles(styles)(TableCars);